@import url('https://fonts.googleapis.com/css?family=VT323');

body {
  margin: 0;
  padding: 0;

  background-color: black;
  color: white;
}

.center {
  display: -ms-flexbox;
  display: flex;

  width: 100vw;
  height: 100vh;

  -ms-flex-pack: center;

      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.clock {
  color: red;
  font-size: 4em;
  font-family: 'VT323', monospace;

  padding: 10px;
  border: 1px ridge red;
  border-radius: 10px;
  background-color: rgba(255, 0, 0, 0.1);

  -webkit-animation: pulse 0.5s ease-in 0s infinite alternate-reverse;

          animation: pulse 0.5s ease-in 0s infinite alternate-reverse;

  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

@-webkit-keyframes pulse {
  from { -webkit-box-shadow: 0 0 1px red; box-shadow: 0 0 1px red }
  to { -webkit-box-shadow: 0 0 10px red; box-shadow: 0 0 10px red }
}

@keyframes pulse {
  from { -webkit-box-shadow: 0 0 1px red; box-shadow: 0 0 1px red }
  to { -webkit-box-shadow: 0 0 10px red; box-shadow: 0 0 10px red }
}



